
.input-div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 2rem;
    color: #fff;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.input-div h1 {
    text-align: center;
    margin: 8rem 0 2rem;
    font-size: 3.5rem;
}

.input-div h3 {
    font-size: 1.75rem;
    margin-bottom: 1rem;
}

.input-div input {
    width: 50px;
    height: 50px;
    margin-top: 0.5rem;
    padding-left: 0.5rem;
    font-size: 1.75rem;
    text-align: center;
    background: transparent;
    border-radius: 1rem;
    color: #fff;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.categories {
    display: grid;
    grid-template-columns: repeat(4, auto);
    justify-content: center;
    row-gap: 50px;
    column-gap: 20px;
    margin: 2rem 0;
}

.category-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20%;
    width: 300px;
    height: 200px;
    background: #C4B5FD;
    border: 1px solid black;
    border-radius: 2rem;
    text-decoration: none;
    color: #000;
    cursor: pointer;
}

.category-card:hover {
    background: #A855F7;
}

img {
    width: 75px;
    height: 75px;
}


@media only screen and (max-width: 1440px) {
    .category-card {
        scale: 0.8;
    }
    
}

@media only screen and (max-width: 1024px) {
    .category-card {
        scale: 0.7;
    }
    .categories {
        margin: 0;
        padding: 0;
    }
}