

nav {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    top: 0;
    width: 100%;
    height: 80px;
    position: fixed;
    background-color: #374151;
    z-index: 1;
}

nav a {
    display: flex;
    margin-left: 2rem;
    align-items: center;
    color: whitesmoke;
    text-decoration: none;
    font-size: 1.75rem;
}

nav div {
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    font-size: 1.25rem;
}

nav div a img {
    width: 150px;
    height: auto;
}