
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 6rem;
}

.question {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70vw;
    height: 200px;
    margin: 2rem auto;
    background-color: #FAFAFA;
    border-radius: 2rem;
    border: 1px solid #374151;
}

.question h1 {
    margin: 2rem;
}

.quiz {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.alternatives-div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin-bottom: 3rem;
}

.alternatives-div button {
    height: 125px;
    width: 400px;
    background-color: #C5C7CB;
    color: #374151;
    font-size: 2rem;
    padding: 1rem;
    border-radius: 3rem;
    cursor: pointer;
}

.alternatives-div button:hover {
    background-color: #95979B;
}

.num-question p {
    font-size: 1.25rem;
    margin-bottom: 1rem;
}

.scroll-btn-div {
    display: inline-flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
}

.scroll-btn-div button {
    width: 300px;
    height: 80px;
    background: conic-gradient(at right top, rgb(199, 210, 254), rgb(71, 85, 105), rgb(199, 210, 254));
    border-radius: 3rem;
    font-size: 2rem;
}

.scroll-btn-div button:hover {
    background: conic-gradient(at right top, rgb(139, 150, 139), rgb(54, 68, 85), rgb(139, 150, 139));
    color: whitesmoke;
}

.score-div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}

.score-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 200px;
    background-color: #FAFAFA;
    border-radius: 4rem;
    border: 1px solid #374151;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.score-container p {
    font-size: 1.75rem;
}
